html,body{
  height: 100%;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}
.icons-wrapper{
display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
}
.icon{
  margin: 10px;
}
.App {
  text-align: center;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
